@import "../../styles/variables";

.dialogs {
  &__search {
    padding: 2px 20px 20px 20px;
    input {
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      --antd-wave-shadow-color: #1890ff;
      --scroll-bar: 0;
      font-family: inherit;
      overflow: visible;
      box-sizing: border-box;
      margin: 0;
      font-variant: tabular-nums;
      list-style: none;
      font-feature-settings: 'tnum', "tnum";
      position: relative;
      display: inline-block;
      width: 100%;
      min-width: 0;
      color: rgba(0, 0, 0, 0.65);
      font-size: 14px;
      line-height: 1.5715;
      background-image: none;
      border-radius: 2px;
      transition: all 0.3s;
      -webkit-appearance: none;
      touch-action: manipulation;
      background-color: #f7f8f9;
      height: 38px;
      text-overflow: ellipsis;
      padding: 0;
      border: none;
      outline: none;
      padding: 10px;      
    }
    // .ant-input {
    //   background-color: #f7f8f9;
    //   border: 0;
    //   height: 38px;
    //   padding: 4px 11px 6px;
    // }
  }

  &__item {
    $item: &;
    display: flex;
    margin-bottom: 20px;

    // &:hover {
    //   background-color: #f3f7ff;
    // }

    // &--selected,
    // &--selected:hover {
    //   background-color: #1890ff;
    //   color: #fff !important;
    // }

    &-avatar {
      position: relative;
      flex: 0 0 40px;
      width: 40px;
      height: 40px;
      margin-right: 10px;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }

    &--online {
      #{$item}-avatar {
        &::before {
          display: block;
          position: absolute;
          z-index: 1;
          bottom: 0px;
          right: 2px;
          content: "";
          background-color: #4caf50;
          border-radius: 30px;
          width: 9px;
          height: 9px;
          border: solid 1.5px white;
        }
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex: 1;
      &-top {
        display: flex;
        b {
          font-weight: 500;
          font-size: 14px;
          flex: 1;
          margin-bottom: 6px;
        }
        span {
          opacity: 0.5;
        }
      }
      &-bottom {
        position: relative;
        p {
          opacity: 0.7;
          text-overflow: ellipsis;
          overflow: hidden;
          width: 220px;
          height: 1.2em;
          white-space: nowrap;
        }
        &-count {
          position: absolute;
          right: 0;
          bottom: 0;
          background: #f46b6b;
          color: #fff;
          font-weight: bold;
          width: 100%;
          min-width: 18px;
          max-width: 22px;
          height: 18px;
          border-radius: 30px;
          font-size: 11px;
          text-align: center;
          line-height: 17px;
          box-shadow: 0 0 0px 3px #fff;
        }
        .message__icon-readed {
          bottom: 0;
          left: auto;
          right: 0;
        }
      }
    }
  }
}
