.emoji {
  direction: ltr;
  text-align: left;
  cursor: text;
  font-size: 13px;
  font-family: -apple-system,BlinkMacSystemFont,Roboto,Helvetica Neue,Geneva,"Noto Sans Armenian","Noto Sans Bengali","Noto Sans Cherokee","Noto Sans Devanagari","Noto Sans Ethiopic","Noto Sans Georgian","Noto Sans Hebrew","Noto Sans Kannada","Noto Sans Khmer","Noto Sans Lao","Noto Sans Osmanya","Noto Sans Tamil","Noto Sans Telugu","Noto Sans Thai",sans-serif,arial,Tahoma,verdana;
  line-height: 17px;
  width: 16px;
  height: 16px;
  border: none;
  vertical-align: -3px;
  margin: 0 1px;
  display: inline-block;
  overflow: hidden;
  color: #000!important;
  font-weight: 400;
  -webkit-font-smoothing: subpixel-antialiased;
  }
  #input-container {
  text-size-adjust: 100%;
  font-size: 14px;
  -webkit-font-smoothing: subpixel-antialiased;
  font-family: -apple-system,BlinkMacSystemFont,Roboto,Segoe UI,Segoe WP,Helvetica Neue,Lucida Grande,Verdana,Tahoma,Arial,sans-serif!important;
  -webkit-box-direction: normal;
  position: absolute;
  top: 4px;
  bottom: 4px;
  left: 44px;
  right: 90px;
  margin: 0;
  padding: 0;
  }
  #input-box {
   
    font-weight: 400;
    -webkit-font-smoothing: subpixel-antialiased;
    direction: ltr;
    text-align: left;
    cursor: text;
    -webkit-user-modify: read-write;
    -webkit-line-break: after-white-space;
    font-size: 13px;
    font-family: -apple-system,BlinkMacSystemFont,Roboto,Helvetica Neue,Geneva,"Noto Sans Armenian","Noto Sans Bengali","Noto Sans Cherokee","Noto Sans Devanagari","Noto Sans Ethiopic","Noto Sans Georgian","Noto Sans Hebrew","Noto Sans Kannada","Noto Sans Khmer","Noto Sans Lao","Noto Sans Osmanya","Noto Sans Tamil","Noto Sans Telugu","Noto Sans Thai",sans-serif,arial,Tahoma,verdana;
    box-shadow: none;
    background: var(--white);
    -webkit-appearance: none;
    color: var(--black);
    margin: 0;
    outline: 0;
    min-height: 36px;
    width: 100%;
    resize: none;
    max-height: 200px;
    overflow-y: auto;
    overflow-x: hidden;
    border-radius: 6px;
    line-height: 17px;
    padding-top: 9px;
    padding-bottom: 10px;
    padding-left: 13px;
    margin-bottom: 0;
    box-sizing: border-box;
    border: none;
    transition: border-bottom-color 0.3s linear;
    padding-right: 74px;
  }
  
#input-box {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  --antd-wave-shadow-color: #1890ff;
  --scroll-bar: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  outline: none;
  box-sizing: border-box;
  margin: 0;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: 'tnum', "tnum";
  position: relative;
  display: inline-block;
  width: 100%;
  min-width: 0;
  color: rgba(0, 0, 0, 0.65);
  line-height: 1.5715;
  background-color: #fff;
  background-image: none;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  transition: all 0.3s;
  padding: 6.5px 11px;
  font-size: 16px;
}

[contenteditable="true"].single-line {
  white-space: nowrap;
  width:100px;
  overflow: hidden;
} 
[contenteditable="true"].single-line br {
  display:none;
}
[contenteditable="true"].single-line div {
  display:none;
}
[contenteditable="true"].single-line * {
  display:inline;
  white-space:nowrap;
}